/* CARD STYLE */
 .card-grid2 {
     display: grid;
     -ms-flex-pack: space-evenly;
         justify-content: space-evenly;
     grid-template-columns: 30% 30%;
     grid-gap: 20px;
     padding-bottom: 2em;
}
 .about {
     background-color: #4d4d4d;
}

 .about-title {
     font-weight: 400;
     padding-bottom: 2em;
}
 .about-intro {
     margin: 4em auto;
     max-width: 54%;
     padding-top: 6em;
}
 .about-description {
     padding-bottom: 2em;
}
.about-skills {
  text-align: left;
}
#soft-skills {
  width: 90%;
}
 #jc-img {
     width: 84px;
     height: 112px;
     border-radius: 4px;
     margin: 20px 30px;
}
 .card-box2 {
     padding-bottom: 6em;
}
/* Smartphones (portrait and landscape) ----------- */
  @media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
     .card-grid2 {
         width: 100%;
         display: block;
    }
    .card-box2 {
      padding-bottom: 0;
    }
    .about-title {
        padding-bottom: 0.5em;
   }
     .about-intro {
         width: 100%;
         max-width: 100%;
    }
     .about-description {
         display: block;
         padding-bottom: 0.5em;
    }
    #soft-skills {
      width: 100%;
    }
     #jc-img {
         width: 84px;
         height: 112.5px;
    }
}

/*General Styles*/

* {
	margin: 0;
	padding: 0;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

ul,
nav {
	list-style: none;
}

section {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	padding: 100px 80px;
}

.grid {
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: center;
	    justify-content: center;
}

hr {
	width: 250px;
	height: 3px;
	background-color: #f5f5f5;
	border: 0;
	margin-bottom: 50px;
}

section h3.title {
	text-transform: capitalize;
	font: bold 32px "Open Sans", sans-serif;
	margin-bottom: 30px;
	text-align: center;
}

section p {
	max-width: 775px;
	line-height: 2;
	padding: 0 20px;
	margin-bottom: 30px;
	text-align: center;
}

@media (max-width: 800px) {
	section {
		padding: 50px 20px;
	}
}

/*Packages Section*/

.packages .grid li {
	padding: 50px;
	-ms-flex-preferred-size: 50%;
	    flex-basis: 50%;
	text-align: center;
}

.packages .grid li i {
	color: #f5f5f5;
}

.packages .grid li h4 {
	font-size: 30px;
	margin: 25px 0;
}

@media (max-width: 800px) {
	.packages .grid li {
		-ms-flex-preferred-size: 100%;
		    flex-basis: 100%;
		padding: 20px;
	}
}



